@use "~admin-stylesheets/colors";

.nab-welcome-message {
	background: #fff;
	border-radius: 2px;
	box-shadow:
		0 2px 0 #ddd,
		0 2px 10px 0 colors.$color__box-shadow;
	display: block;
	margin: 5em auto 1em;
	max-width: 40em;
	padding: 2em;
	text-align: center;

	&__logo {
		max-width: 20em;
	}

	&__intro {
		font-size: 18px;
	}

	&__policy {
		max-width: 90%;
		margin: 0 auto;
		text-align: left;

		.components-base-control__field {
			display: flex;
			flex-direction: row;
		}

		.components-checkbox-control__input-container {
			margin-top: 0.2em;
		}
	}

	&__actions {
		border-top: 1px solid colors.$color__border-lighter;
		margin: 2em auto 0;
		max-width: 90%;
		padding-top: 2em;
	}

	&__start.is-large {
		font-size: 18px;
		height: auto;
		padding: 0.5em 1.5em;
	}
}
